const validate = (type, value) => {
  var fulltextRegex = /^([A-z]+) ([A-z]+)$/
  var longtextRegex = /^([a-zA-Z0-9]+)$/
  var emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/
  var phoneRegex = /^([0-9]{9,16})$/
  var passwordRegex = /^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{6,})/
  var otpRegex = /(\S{7})/
  var serialNumberRegex = /(\S{10,})/
  var linkRegex = /^(?:(?:https?|ftp):\/\/)(?:\S+(?::\S*)?@)?(?:(?!10(?:\.\d{1,3}){3})(?!127(?:\.​\d{1,3}){3})(?!169\.254(?:\.\d{1,3}){2})(?!192\.168(?:\.\d{1,3}){2})(?!172\.(?:1[​6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1​,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00​a1-\uffff0-9]+-?)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]+-?)*[a-z\u​00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/[^\s]*)?$/i
  var generalRegex = /(\S+)/
  var numberRegex = /(\d+)/
  var usernameRegex = /^(?!\s)(?!.*\s$)(?=.*[a-zA-Z0-9])[a-zA-Z0-9 '~?!]{2,}$/
  var priceRegex = /^[1-9]\d{0,7}(?:\.\d{1,4})?$/
  var accountNumberRegex = /^(\d){8,11}$/
  var bvnRegex = /^(\d){15}$/
  var upperNumberAndLetter = /^([A-Z]+)*$/
  var specialChar = /[^a-zA-Z0-9]/

  if (type === 'fulltext') {
    if (fulltextRegex.test(value)) return true
    return false
  } else if (type === 'username') {
    if (usernameRegex.test(value)) return true
    return false
  } else if (type === 'upperNumberandLetter') {
    if (upperNumberAndLetter.test(value)) return true
    return false
  } else if (type === 'password checker') {
    var lowerCaseRegex = /(?=.*[a-z])/
    var upperCaseRegex = /(?=.*[A-Z])/
    var numberRegex = /(?=.*[0-9])/
    var symbolRegex = /(?=.*[!@#$%&\*])/

    if (!lowerCaseRegex.test(value)) {
      return 'Password must contain at least a lower case'
    } else if (!upperCaseRegex.test(value)) {
      return 'Password must contain at least an upper case'
    } else if (!numberRegex.test(value)) {
      return 'Password must contain atleast one number'
    } else if (!symbolRegex.test(value)) {
      return 'Password must contain atleast one symbol'
    } else if (value.length < 8) {
      return 'Password must be 8 characters long'
    } else {
      return true
    }
  } else if (type === 'longtext') {
    if (longtextRegex.test(value)) return true
    return false
  } else if (type === 'serial') {
    if (serialNumberRegex.test(value)) return true
    return false
  } else if (type === 'number') {
    if (numberRegex.test(value)) return true
    return false
  } else if (type === 'email') {
    if (emailRegex.test(value)) return true
    return false
  } else if (type === 'phone') {
    if (phoneRegex.test(value)) return true
    return false
  } else if (type === 'price') {
    if (priceRegex.test(value)) return true
    return false
  } else if (type === 'password') {
    if (passwordRegex.test(value)) return true
    return false
  } else if (type === 'otp') {
    if (otpRegex.test(value)) return true
    return false
  } else if (type === 'link') {
    if (linkRegex.test(value)) return true
    return false
  } else if (type === 'general') {
    if (generalRegex.test(value)) return true
    return false
  } else if (type === 'account_number') {
    if (accountNumberRegex.test(value)) return true
    return false
  } else if (type === 'bvn') {
    if (bvnRegex.test(value)) return true
    return false
  }
}

export default validate
