import Vue from 'vue'
import store from '@/store'
import router from '@/router'
import axios from 'axios'
import ls from '@/functions/localstorage'

const server = () => {
  const rawUri = store.state.rawURI
  Vue.prototype.$server = {
    login: async (username, pass) => {
      const requestPayload = {
        email: username,
        password: pass,
      }
      await axios.post(rawUri + 'user/login', requestPayload)
        .then(response => {
          // console.log(response.data.data)
          const storeTicket = {
            token: response.data.data.token,
            token_generation: response.data.data.expires_at * 1000,
          }
          const storeFansData = {
            id: response.data.data.id,
            username: response.data.data.username,
            email: response.data.data.email,
            user_type: response.data.data.user_type,
          }
          if (ls.clearStorage('logisticsToken') && ls.clearStorage('logisticsData')) {
            ls.setStorage('logisticsToken', storeTicket)
            ls.setStorage('logisticsData', storeFansData)
          }
          store.commit('UPDATE_USER', storeFansData)
        })
        .catch(error => {
          console.log(error)
        })
    },
    logout: async () => {
      router.push('/user/login')
      ls.clearStorage('logisticsData')
      ls.clearStorage('logisticsToken')
      // store.commit('UPDATE_USER', {})
      // await axios.post(rawUri + 'user/logout')
      //   .then(response => {
      //     console.log(response)
      //   })
      //   .catch(error => {
      //     console.log(error)
      //   })
    },
    get: async (url) => {
      await axios.get(rawUri + url)
        .then(response => {
          return response.data.data
        })
        .catch(error => {
          console.log(error)
        })
    },
    post: async (url, params = {}) => {
      await axios.post(rawUri + url, params.payload)
        .then(response => {
          console.log(response)
        })
        .catch(error => {
          console.log(error)
        })
    },
    put: async () => {
      return 'fff'
    },
    delete: async () => {
      return 'fff'
    },
  }
}

export default server
