<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import ls from './functions/localstorage'

export default {
  name: 'app',
  computed: {
    ...mapGetters(['user']),
    nextRoute() {
      return this.$route.query.redirect || '/'
    },
  },
  mounted() {
    const auth = this.user
    if (Array.isArray(auth) && auth.length) {
      // console.log('valid')
    } else {
      // console.log('no valid')
      if (ls.getStorage('logisticsData')[0]) {
        this.$store.commit('UPDATE_USER', ls.getStorage('logisticsData')[0])
        if (this.$route.path === '/user/login') {
          this.$router.replace(this.nextRoute)
        }
      } else {
        if (this.$route.path !== '/user/login') {
          this.$router.push('/user/login')
        }
      }
    }
  },
  watch: {
    user(auth) {
      // console.log('auth ', auth)
      if (auth) {
        if (this.$route.path === '/user/login') {
          window.location = this.nextRoute
          // this.$router.replace(this.nextRoute)
        }
      } else if (!auth || ls.getStorage('logisticsData')[0]) {
        this.$store.commit('UPDATE_USER', ls.getStorage('logisticsData')[0])
        if (this.$route.path === '/user/login') {
          this.$router.replace(this.nextRoute)
        }
      } else {
        this.$router.push('/user/login')
      }
    },
    '$route'(to, from) {
      const query = Object.assign({}, to.query)
      this.$store.commit('SETUP_URL_SETTINGS', query)
    },
  },
}
</script>
<style>
  .br {
    border: solid 1px #000;
  }
</style>
