import axios from 'axios'
import store from '../store'
// import router from '../router.js'
// import ls from '../functions/localstorage'
const DEBUG = process.env.NODE_ENV === 'development'

// here we declare the user tokens for authentication
// let userToken = ls.passport || sessionStorage.

// This part runs only on full page load
var userToken = localStorage.logisticsToken || null
userToken = JSON.parse(userToken)

// console.log('from server', userToken !== null ? userToken[0].token : null )

// creating unique sting in base64
// var encodedString = btoa('_$_MDD$___F53cd')

var instance = axios.create({
  // baseURL: "https://matchdaydeals.com/api/",
  baseURL: store.state.rawURI,
  timeout: 60000,
  // headers: {"Authorization": ("Bearer " + (userToken !== null ? userToken[0].token : null)), "Content-Type": "application/json; charset=utf-8;text/html","M_Flid":encodedString,"M-Channel": "web" }
  headers: { Authorization: ('Bearer ' + (userToken !== null ? userToken[0].token : null)), 'Content-Type': 'application/json; charset=utf-8;text/html' },
})

axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  var userToken = localStorage.ticket || null
  userToken = JSON.parse(userToken)
  config.headers['Authorization'] = 'Bearer ' + (userToken !== null ? userToken[0].token : null)
  return config
}, function (error) {
  // Do something with request error
  if (DEBUG) {
    console.error('✉️ ', error)
  }
  return Promise.reject(error)
})

export default instance
