import Vue from 'vue'
import Router from 'vue-router'
import LoginLayout from '@/layouts/Login'
import MainLayout from '@/layouts/Main'
import store from '@/store'

Vue.use(Router)

const router = new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      redirect: 'dashboard/alpha',
      component: MainLayout,
      meta: {
        authRequired: true,
        hidden: true,
      },
      children: [
        // Dashboards
        {
          path: '/dashboard/alpha',
          meta: {
            title: 'Dashboard Alpha',
          },
          component: () => import('./views/dashboard/alpha'),
        },
        {
          path: '/dashboard/customer_registration',
          meta: {
            title: 'Customer Registration',
          },
          component: () => import('./views/customer_management/'),
        },
        {
          path: '/dashboard/customer_data',
          meta: {
            title: 'Customer Data',
          },
          component: () => import('./views/customer_management/customer_data'),
        },
        {
          path: '/dashboard/analytics',
          meta: {
            title: 'Analytics',
          },
          component: () => import('./views/analytics/'),
        },
        {
          path: '/dashboard/reports',
          meta: {
            title: 'Reports',
          },
          component: () => import('./views/analytics/reports'),
        },
        {
          path: '/dashboard/invoices',
          meta: {
            title: 'Invoices',
          },
          component: () => import('./views/analytics/invoices'),
        },
        {
          path: '/dashboard/invoices/:invoice_number',
          name: 'view_invoice',
          props: true,
          meta: {
            title: 'View Invoice',
          },
          component: () => import('./views/analytics/view_invoice'),
        },
        {
          path: '/dashboard/shipment',
          name: 'Shipment',
          meta: {
            title: 'All Shipments',
          },
          component: () => import('./views/shipments/index'),
        },
        {
          path: '/dashboard/shipment/create',
          name: 'create_shipment',
          meta: {
            title: 'Create Shipments',
          },
          component: () => import('./views/shipments/create'),
        },
        {
          path: '/dashboard/user',
          name: 'Users',
          meta: {
            title: 'All Users',
          },
          component: () => import('./views/user/management/index'),
        },
        {
          path: '/dashboard/user/create',
          name: 'CreateUsers',
          meta: {
            title: 'Create Admin Users',
          },
          component: () => import('./views/user/management/create'),
        },
        {
          path: '/dashboard/issues/',
          name: 'Issues',
          meta: {
            title: 'Issues',
          },
          component: () => import('./views/issues'),
        },
        {
          path: '/dashboard/issues/create',
          name: 'CreateIssues',
          meta: {
            title: 'Create Issues',
          },
          component: () => import('./views/issues/create'),
        },
        {
          path: '/dashboard/sms',
          name: 'sms',
          meta: {
            title: 'All Sms Message',
          },
          component: () => import('./views/sms/index'),
        },
        {
          path: '/dashboard/sms/create',
          name: 'CreateSms',
          meta: {
            title: 'Send Sms Message',
          },
          component: () => import('./views/sms/create'),
        },
        {
          path: '/dashboard/history/transactions',
          name: 'transactionsHistory',
          meta: {
            title: 'Transactions History',
          },
          component: () => import('./views/history/transactions'),
        },
        {
          path: '/dashboard/history/activities',
          name: 'activitiesHistory',
          meta: {
            title: 'Activities History',
          },
          component: () => import('./views/history/activities'),
        },
        {
          path: '/dashboard/expenditure/create',
          name: 'createExpenditure',
          meta: {
            title: 'Create Expenditure',
          },
          component: () => import('./views/analytics/create_expenditure'),
        },

        // 404
        {
          path: '/404',
          meta: {
            title: '404',
          },
          component: () => import('./views/404'),
        },
      ],
    },

    // System Pages
    {
      path: '/user',
      component: LoginLayout,
      redirect: '/user/login',
      children: [
        {
          path: '/user/login',
          meta: {
            title: 'Login',
          },
          component: () => import('./views/user/login'),
        },
      ],
    },

    // Redirect to 404
    {
      path: '*', redirect: '/404', hidden: true,
    },
  ],
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.authRequired)) {
    if (!store.state.user.user) {
      next({
        path: '/user/login',
        query: { redirect: to.fullPath },
      })
    } else {
      next()
    }
  } else {
    next()
  }
})

export default router
